/* eslint-disable jsx-a11y/img-redundant-alt */
import React, { useRef, useEffect } from "react";
import styled from "@emotion/styled";
import { motion, useAnimation } from "framer-motion";
import useOnScreen from "../hooks/useOnScreen";
import ContentWrapper from "../styles/ContentWrapper";
import aboutImage from "../content/about/about.jpg";
import Image from "../styles/Image";

const StyledSection = styled.section`
    width: 100%;
    height: auto;
    background: #121212;
    margin-top: 4rem;
`;

const StyledContentWrapper = styled(ContentWrapper)`
    && {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        @media (min-width: 768px) {
            flex-direction: row;
            justify-content: space-between;
        }
        .section-title {
            margin-bottom: 3rem;
        }
        .inner-wrapper {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        .text-content {
            width: 100%;
            max-width: 31.25rem;
        }
        @media (max-width: 568px) {
            .text-content {
                width: 85%;
            }
        }
        .image-content {
            width: 100%;
            max-width: 85vw;
            margin-top: 4rem;
            margin-left: 0;
            @media (min-width: 768px) {
                margin-left: 2rem;
                max-width: 22rem;
            }
        }
        .about-author {
            border-radius: 1rem;
            box-shadow: 0 0 2.5rem rgba(0, 0, 0, 0.16);
            filter: grayscale(20%) contrast(1) brightness(90%);
            transition: all 0.3s ease-out;
            &:hover {
                filter: grayscale(50%) contrast(1) brightness(90%);
                transform: translate3d(0px, -0.125rem, 0px);
                box-shadow: 0 0 2.5rem rgba(0, 0, 0, 0.32);
            }
        }
    }
`;

const About = () => {
    const tControls = useAnimation();
    const iControls = useAnimation();

    const tRef = useRef<HTMLDivElement | null>(null);
    const tOnScreen = useOnScreen(tRef);

    const iRef = useRef<HTMLDivElement | null>(null);
    const iOnScreen = useOnScreen(iRef);

    useEffect(() => {
        if (tOnScreen) tControls.start({ opacity: 1, y: 0 });
        if (iOnScreen) iControls.start({ opacity: 1, x: 0 });
    }, [tControls, iControls, tOnScreen, iOnScreen]);

    return (
        <StyledSection id="about">
            <StyledContentWrapper>
                <motion.div
                    className="inner-wrapper"
                    ref={tRef}
                    initial={{ opacity: 0, y: 20 }}
                    animate={tControls}
                >
                    <h3 className="section-title">About Me</h3>
                    <div
                        className="text-content"
                        style={{ whiteSpace: "pre-wrap" }}
                    >
                              .{"\n"}
                        Doubledown Interactive    Unity     SDK  ,  Nexon Korea  UnityC++    Flutter    .
                        {"\n\n"}
                             ,       .{"\n"}
                                .{"\n\n"}
                    </div>
                </motion.div>
                <motion.div
                    className="image-content"
                    ref={iRef}
                    initial={{ opacity: 0, x: 20 }}
                    animate={iControls}
                >
                    <Image src={aboutImage} alt="" loading="lazy" />
                </motion.div>
            </StyledContentWrapper>
        </StyledSection>
    );
};

export default About;
